import {Component, Renderer, ElementRef, OnDestroy} from '@angular/core';

@Component({
  selector: 'bind-event-renderer',
  template : 'Hello world! <button (click)="removeListeners()">Remove listeners!</button>'
})
export class BindEventRendererComponent implements OnDestroy {
  listenFunc: Function;
  globalListenFunc: Function;
  constructor(elementRef: ElementRef, renderer: Renderer) {
    this.listenFunc = renderer.listen(elementRef.nativeElement, 'click', (event:any) => {
      console.log(event);
      console.log('Element clicked');
    });
    
    this.globalListenFunc = renderer.listenGlobal('document', 'click', (event:any) => {
      //  console.log(event);
      //  console.log('Document clicked');
    });
  }
  
  removeListeners() {
    this.listenFunc();
    this.globalListenFunc();
  }
  
  ngOnDestroy() {
    
    // Remove the listeners!
    this.listenFunc();
    this.globalListenFunc();
  }
}
